import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import {
  InlineWrapper,
  StyledForm,
  Product,
  ProductQuantity,
  ProductWrapper,
  ImageWrapper,
  MacroWrapper,
} from './FindIngredient.style';
import Select from 'components/atoms/Select/Select';
import { useForm, Controller } from 'react-hook-form';
import { db } from 'assets/firebase/firebase';
import FormField from 'components/molecules/FormField/FormField';
import Paragraph from 'components/atoms/Paragraph/Paragraph';
import { useMedia } from 'hooks/useMedia';
import Button from 'components/atoms/Button/Button';

const countMacro = (protein, carbs, fat, ingredientQuantity) => {
  const currentProtein = Number(
    ((protein * ingredientQuantity) / 100).toFixed(1)
  );
  const currentCarbs = Number(((carbs * ingredientQuantity) / 100).toFixed(1));
  const currentFat = Number(((fat * ingredientQuantity) / 100).toFixed(1));
  const totalCalories = (currentProtein + currentCarbs) * 4 + currentFat * 9;

  return {
    protein: currentProtein,
    carbs: currentCarbs,
    fat: currentFat,
    calories: totalCalories,
  };
};

const countCalories = (protein, carbs, fat, ingredientQuantity) => {
  Number(
    (((protein * 4 + carbs * 4 + fat * 9) * ingredientQuantity) / 100).toFixed(
      1
    )
  );
};

const FindIngredient = ({closeModal}) => {
  const media = useMedia('(min-width: 800px)');
  const [products, setProducts] = useState([]);
  const {
    register,
    handleSubmit,
    control,
    watch,
    formState: { errors },
  } = useForm();

  const currentTypeValue = watch('ingredientType');
  const quantityValue = watch('ingredientQuantity');
  const unitValue = watch('ingredientUnit');
  const renderOptions = products.filter(
    (item) => item.value === currentTypeValue
  );
  const defaultUnitOption = [{ value: '-', label: '-' }];

  const onSubmit = (data) => {
    console.log(data);
  };

  useEffect(() => {
    const ref = db.ref('/products');

    const listener = ref.on('value', (snapshot) => {
      const data = snapshot.val();

      let unitsData = [];
      const temporaryData = [];
      for (let id in data) {
        for (let i in data[id].units) {
          unitsData.push({
            label: data[id].units[i].name,
            value: {
              name: data[id].units[i].name,
              converter: data[id].units[i].value,
            },
          });
        }
        temporaryData.push({
          value: {
            path: data[id].imagePath,
            nameExtended: data[id]?.nameExtended,
            name: id,
            carbs: data[id].carbs,
            fat: data[id].fat,
            protein: data[id].protein,
          },
          label: id,
          unitsData,
        });
        unitsData = [];
      }

      setProducts(temporaryData);
    });

    return () => ref.off('value', listener);
  }, []);

  const countedMacro =
    currentTypeValue &&
    quantityValue &&
    unitValue &&
    countMacro(
      currentTypeValue.protein,
      currentTypeValue.carbs,
      currentTypeValue.fat,
      quantityValue
    );

  console.log(currentTypeValue, errors);
  return (
    <StyledForm onSubmit={handleSubmit(onSubmit)}>
      <Paragraph size={media ? 'big' : 'medium'} customMargin="30px 0 0" isBold>
        Dodaj produkt:
      </Paragraph>
      <Controller
        control={control}
        rules={{ required: true }}
        name="ingredientType"
        render={({ field: { onChange, value, ref, name } }) => (
          <Select
            inputRef={ref}
            id="select"
            placeholder="wybierz produkt..."
            onChange={onChange}
            value={value}
            optionsValue={products}
            name={name}
          />
        )}
      />

      <InlineWrapper>
        <FormField
          small
          {...register(`ingredientQuantity`, {
            required: true,
          })}
          type="number"
          name="ingredientQuantity"
          id="ingredientQuantity"
          placeholder="ilość"
        />
        <Controller
          control={control}
          name="ingredientUnit"
          rules={{ required: true }}
          render={({ field: { onChange, value, ref, name } }) => (
            <Select
              inputRef={ref}
              id="select"
              inputSize="small"
              onChange={onChange}
              value={value}
              optionsValue={
                currentTypeValue
                  ? renderOptions[0]?.unitsData
                  : defaultUnitOption
              }
              name={name}
            />
          )}
        />
      </InlineWrapper>

      {quantityValue && unitValue && currentTypeValue ? (
        <ProductWrapper>
          <ImageWrapper>
            <img src={currentTypeValue.path} alt={currentTypeValue.name} />
          </ImageWrapper>
          <span>{`${quantityValue} ${unitValue.name}`}</span>
          <MacroWrapper>
            <span>B: {countedMacro.protein}</span>
            <span>W: {countedMacro.carbs}</span>
            <span>T: {countedMacro.fat}</span>
          </MacroWrapper>
          <span>{countedMacro.calories}kcal</span>
        </ProductWrapper>
      ) : null}

      <InlineWrapper>
        <Button type="submit">Dodaj</Button>
        <Button onClick={closeModal} secondary>Wróć</Button>
      </InlineWrapper>
    </StyledForm>
  );
};

FindIngredient.propTypes = {};

export default FindIngredient;
